<template>
	<view class="body">
		<view class="content">
			<view class="text">
				<view class="title">本题错误详情或建议</view>
				<textarea name="" id="" cols="30" rows="10" placeholder="输入详情纠错内容" v-model="content"></textarea>
			</view>
			<view class="imgs">
				<view class="title">图片(选填，提供图片截图)</view>
				<img v-if="file.length!=0" :src="file" mode="aspectFit" @click="preview">
				<view class="add" @click="choosePhoto">+</view>
			</view>
		</view>
		<button class="submit" @click="submit">点击提交</button>
	</view>
</template>

<script>
	import store from '@/store/index.js'; //引入store
	import { correction, uploadFile } from '@/common/http.api.js';
	export default{
		data() {
			return {
				token:'',
				file:[],
				item_id:'',
				content:'',
			}
		},
		onLoad(option) {
			this.token = store.state.vuex_token
			this.item_id = option.item_id
		},
		methods: {
			// 选择图片
			choosePhoto(){
				uni.chooseImage({
					count: 1, 
					sourceType: ['album'], 
					sizeType: ['original'],
					success:(res)=>{
						console.log('选择图片完成', res)
						console.log(res.tempFilePaths[0])
						// 调用上传图片的接口
						this.file = res.tempFilePaths[0]
						
						// console.log(res.tempFiles)
					},
					fail(err) {
						console.log('失败', err);
					},
					complete() {
						console.log('结束');
					},
				})
			},
			// 预览图片
			preview() {
				let file = []
				file.push(this.file)
				uni.previewImage({
					urls: file
				});
			},
			// 提交信息
			submit(){
				console.log(this.content)
				console.log(this.file)
				if(this.file.length!=0){
					uni.uploadFile({
						url:'https://api.ydwechat.com/apiindex.php/xcx.Politics/uploadFile',
						header:{
							// HTTP 请求 Header, header 中不能设置 Referer
						},
						filePath: this.file, // 要上传文件资源的路径
						name: 'file', // 文件对应的 key , 开发者在服务器端通过这个 key 可以获取到文件二进制内容
						formData:{
							
						},
						success:(res)=> {
							console.log(res)
							let ress = JSON.parse(res.data);
							console.log('上传成功', ress);
							correction({token:this.token, content:this.content, item_id: this.item_id ,img:ress.data}).then(res=>{
								console.log(res)
								if(res.data.data=='提交成功'){
									this.$u.toast(res.data.data+',即将跳转返回')
									setTimeout(()=>{
										uni.navigateBack({
											delta:1
										})
									},1500)
								}else{
									this.$u.toast(res.data.data)
								}
							}).catch(err=>{
								console.log(err)
							})
						},
						fail() {
							console.log('失败', err);
						},
						complete() {
							console.log('结束');
						},
					})
				}else{
					correction({token:this.token, content:this.content, item_id: this.item_id }).then(res=>{
						console.log(res)
						if(res.data.data=='提交成功'){
							this.$u.toast(res.data.data+',即将跳转返回')
							setTimeout(()=>{
								uni.navigateBack({
									delta:1
								})
							},1500)
						}else{
							this.$u.toast(res.data.data)
						}
					}).catch(err=>{
						console.log(err)
					})
				}
			}
		}
	}
</script>

<style lang="scss">
	.content{
		background-color: #fff;
		padding: 20rpx 40rpx;
		.text{
			textarea{
				border: 1px solid #f7f7fb;
				padding: 20rpx;
			}
			margin-bottom: 30rpx;
		}
		.imgs{
			img{
				width: 200rpx;
				height: 200rpx;
			}
			.add{
				color: #cfcfcf;
				background-color: #f7f7fb;
				width: 190rpx;
				height: 190rpx;
				text-align: center;
				font-size: 128rpx;
				font-weight: 100;
				border: 1px solid #f7f7fb;
			}
		}
		.title::before{
			content: '';
			display: inline-block;
			width: 8rpx;
			height: 32rpx;
			background-color: #ffd656;
			border-radius: 50rpx;
			margin-right: 20rpx;
		}
		.title{
			margin: 0;
			font-size: 28rpx;
			display: flex;
			align-items: center;
			margin-bottom: 30rpx;
		}
	}
	.submit{
		position: fixed;
		bottom: 40rpx;
		left: 10%;
		width: 80%;
		background-color: #ffd656;
		border-radius: 100rpx;
		line-height: 78rpx;
		color: #fff;
	}
	.body{
		background-color: #f7f7fb;
		height: 92vh;
		padding: 4vh 40rpx;
	}
</style>